import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import "../css/pages/games.scss"
import GameCard from "../components/gamecard"

const Games = () => (
  <Layout staticTheme={0}>
    <SEO title="Games" />
    <div className="games-wrapper">
      <div className="games">
        <GameCard title="Project Arrhythmia" description={`"Project Arrhythmia" is a musical bullet-hell in which all things beautiful are deadly. Avoid the musically timed pulse of blocks and bars, and in the process you might just find out what this mysterious "Project Arrhythmia" is.`} link="/project-arrhythmia" image="videos/pa_art.png" />
        <GameCard title="WallPunk" description="A brand new VR Rhythm / Action game coming soon!" link="/wall-punk" />
      </div>
    </div>
  </Layout>
)

export default Games
